var idPerfil = getFromLocalStorage("id_perfil");
var token = getFromLocalStorage("token");
var mainBanner;
var mainList;
var dataList;
var swiper;
var swiper2;
var listLive;
var verticalCursor = 0;
var horizontalCursor = 0;
var hasLiveChannels;
var headerCursor = 0;
var cursor = 0;
let imgSearchSelected = "../../images/search-selected.png";
let imgSearchUnselected = "../../images/search-unselected.png";
let imgSettingSelected = "../../images/profile-selected.png";
let imgSettingUnselected = "../../images/profile-unselected.png";
let lists;
var activeItem = createTagDiv();
var keepwatchingSwiperIndex;
var onModal = false;
let isKeepWatchingContainerOpen = false;
let isExitAppContainerOpen = false;
let actualBuildIndex;

let digitNumberTimeOut;
let digitNumber = null;

let allContentList = [];
let newContentList = [];
let remainsToBuildLists = [];
let actualBuildIndexactualBuildIndex = 0;
let isBuilding = true;

let oldx = 0;
let oldy = 0;
let mouseTimeout;
let webOsSys = getFromLocalStorage("system");

if (!token) goTo("../../index.html");

async function main() {
  try {
    let container = document.getElementById("container");
    let loadingContainer = document.getElementById("loading-container");

    container.style.display = "none";

    dataList = await getData();
    mainList = await getMain();
    setHasLiveChannels(mainList.hasLiveChannels);
    lists = await getLists();

    buildContentRows();

    loadingContainer.style.display = "none";
    container.style.display = "block";

    mainBanner = mainList.banners;

    addClass("item-0", "selected-item");

    replaceImage("item-5-img", 5, imgSearchSelected, imgSearchUnselected);
    replaceImage("item-6-img", 6, imgSettingSelected, imgSettingUnselected);
    if (hasBanner()) {
      buildCarousel();
    } else {
      setClassNoBanner();
    }
    initializingSwipers();

    setInterval(checkUser, 5000);
    window.onunload = userCloseApp;
  } catch {
    goTo("../profile-select/index.html");
  }
}

function buildContentRows() {
  let container = document.getElementById("container");
  allContentList.forEach(function (element, index) {
    actualBuildIndex = index;
    let containerSection = createTagDiv();
    containerSection.classList.add("container--section");
    containerSection.id = "container-section" + index;

    let containerSectionTitle = createTagH1();
    containerSectionTitle.classList.add("container--section-title");
    containerSectionTitle.id = "section-title";
    containerSectionTitle.innerHTML = element.title;

    let containerSectionSlider = createTagDiv();
    containerSectionSlider.classList.add("container--section-slider");

    let containerSectionList = createTagDiv();
    containerSectionList.classList.add("container--section-list");
    containerSectionList.classList.add("swiper2");

    let containerSectionTrack = createTagDiv();
    containerSectionTrack.classList.add("container--section-track");
    containerSectionTrack.classList.add("swiper-wrapper");
    containerSectionTrack.id = "section-track" + index;

    containerSectionSlider.appendChild(containerSectionList);
    containerSectionList.appendChild(containerSectionTrack);

    containerSection.appendChild(containerSectionTitle);
    containerSection.appendChild(containerSectionSlider);

    container.appendChild(containerSection);

    if (allContentList[index].id != 60 && allContentList[index].id != 21) {
      buildSections("section-track" + index, allContentList[index].result);
    } else if (allContentList[index].title == "Continuar assistindo") {
      buildSectionsKeepWatching(
        allContentList[index].result,
        "section-track" + index
      );
      keepwatchingSwiperIndex = index;
    } else {
      buildSectionLive(allContentList[index].result);
    }
  });
}

function buildSectionLive(param) {
  let containerSectionTrack = document.getElementById("section-track0");

  param.list.forEach(function (element, index) {
    let container = createTagDiv();
    container.classList.add("container--live");
    container.classList.add("swiper-slide");
    container.id = "container-live" + index;

    let containerLive = createTagDiv();
    containerLive.classList.add("container--live-content");

    let p = createTagChannelNumber();
    addClass(p, "channelNumber");
    p.textContent = element.channel_number;

    let containerImg = createTagDiv();
    containerImg.classList.add("container--live-img");
    containerImg.appendChild(p);
    containerImg.onclick = () => {
      setLocalStorage("titulo", element.title);
      setLocalStorage("id", element.id);
      setLocalStorage("tipo", element.type);
      setLocalStorage("position", 0);
      setLocalStorage("id_entry", element.entry_id);
      if (element.id === 0) {
        goTo("../live/index.html");
      } else {
        goTo("../watch/index.html");
      }
    };

    if (element.id === 0) {
      let img = createTagDiv();
      img.classList.add("all-channels");
      let seeMore = createTagH2();
      seeMore.innerHTML = "VER MAIS </br> AO VIVO";
      seeMore.classList.add("see-more");
      img.appendChild(seeMore);
      containerImg.appendChild(img);
    } else {
      let img = createTagImg();
      img.classList.add("live-img");

      let imgString = `TV_${element.highlight}`;

      img.src = buildUrlImage(element.cdn, imgString);
      containerImg.appendChild(img);
    }

    containerLive.appendChild(containerImg);
    container.appendChild(containerLive);

    containerSectionTrack.appendChild(container);
  });
}

function createTagChannelNumber() {
  let p = document.createElement("p");

  return p;
}

async function settingKeepWatchingModalClicks(duration, id, position, type) {
  const restart = document.getElementById("restart-button");
  const continuar = document.getElementById("continue-button");

  let entryId;

  let data = await axios(
    "https://watch.tv.br/v3/title?id_perfil=" +
    idPerfil +
    "&id=" +
    id +
    "&tipo=" +
    type +
    "&token=" +
    token,
    { method: "POST" }
  );

  data = data.data;

  if (type === "serie") {
    let found = data.seasons.find(
      (element) =>
        element.order == dataList.keep_watching[horizontalCursor].season_order
    );

    let found2 = found.videos.find(
      (element) =>
        element.order == dataList.keep_watching[horizontalCursor].episode_order
    );
    entryId = found2.id_kaltura;
    title = found.title;
    episode = found2.order;

    setLocalStorage("duration", found2.duration);
    settingTitleEp(title, episode);
  } else {
    setLocalStorage("duration", data.duration);
    entryId = data.id_kaltura;
  }

  restart.onclick = () => {
    settingIdsWatch(id, type, 0, entryId);
    goTo("../watch/index.html");
  };
  continuar.onclick = () => {
    settingIdsWatch(id, type, position, entryId);
    goTo("../watch/index.html");
  };
}

function buildSectionsKeepWatching(param, container) {
  let containerSectionTrack = document.getElementById(container);
  let image = "";
  let title = "";
  let lyric = "";

  dataList.keep_watching.forEach(function (element, index) {
    if (index <= param.list.length - 1) {
      image = buildUrlImage(param.list[index].cdn, param.list[index].highlight);
      title = param.list[index].title;

      let container = createTagDiv();

      container.onclick = () => {
        isKeepWatchingContainerOpen = true;
        document.getElementsByClassName(
          "keep-watching-container"
        )[0].style.display = "flex";

        settingKeepWatchingModalClicks(
          element.duration,
          element.id,
          element.position,
          element.type
        );
      };

      container.classList.add("container--keep-watching");
      container.classList.add("swiper-slide");
      container.id = "container--keep-watching" + index;

      let containerImg = createTagDiv();
      containerImg.classList.add("container--keep-watching-img");

      let createTag = createTagDiv();
      createTag.classList.add("createDiv");

      let img = createTagImg();
      img.src = image;

      let containerText = createTagDiv();
      containerText.classList.add("container--keep-watching-content");

      let text = createTagH2();
      text.classList.add("text-title");
      text.innerHTML = title;

      let tempEp = createTagDiv();
      tempEp.classList.add("text-temp-ep");
      tempEp.innerHTML =
        element.type === "serie"
          ? "Temporada " +
          element.season_order +
          "| " +
          "Episódio " +
          element.episode_order
          : lyric;

      let progressBarContainer = createTagDiv();
      progressBarContainer.classList.add("container--progress-bar");

      let progressBar = createTagDiv();
      progressBar.classList.add("progress-bar");

      let progressBarBack = createTagDiv();

      progressBarBack.classList.add("progress-bar-background");
      progressBarBack.style.width =
        convertToRoundNumber((element.position * 100) / element.duration) + "%";

      progressBar.appendChild(progressBarBack);
      progressBarContainer.appendChild(progressBar);

      containerImg.appendChild(createTag);
      containerImg.appendChild(img);
      containerText.appendChild(text);
      containerText.appendChild(tempEp);
      container.appendChild(containerImg);
      container.appendChild(containerText);
      container.appendChild(progressBarContainer);

      containerSectionTrack.appendChild(container);
    }
  });
}

function goBack() {
  history.back();
}

window.onload = function () {
  document.addEventListener("keydown", async function (e) {
    switch (e.keyCode) {
      case 8:
      case 10009:
      case 10182:
      case 461:
        e.preventDefault();
        openExitAppContainer();
        break;
      case 37:
        e.preventDefault();
        arrowLeft();
        break;
      case 38:
        e.preventDefault();
        arrowUp();
        break;
      case 39:
        e.preventDefault();
        arrowRight();
        break;
      case 40:
        e.preventDefault();
        arrowDown();
        break;
      case 13:
        e.preventDefault();
        enterAction();
        break;
      case 48:
      case 96:
        enterChannelNumber("0");
        break;
      case 49:
      case 97:
        enterChannelNumber("1");
        break;
      case 50:
      case 98:
        enterChannelNumber("2");
        break;
      case 51:
      case 99:
        enterChannelNumber("3");
        break;
      case 52:
      case 100:
        enterChannelNumber("4");
        break;
      case 53:
      case 101:
        enterChannelNumber("5");
        break;
      case 54:
      case 102:
        enterChannelNumber("6");
        break;
      case 55:
      case 103:
        enterChannelNumber("7");
        break;
      case 56:
      case 104:
        enterChannelNumber("8");
        break;
      case 57:
      case 105:
        enterChannelNumber("9");
        break;
      default:
        break;
    }
  });

  window.addEventListener("auxclick", function (e) {
    if (e.button == 1) {
      enterAction();
    }
  });

  window.addEventListener("wheel", function (event) {
    if (event.deltaY < 0) {
      arrowUp();
    } else if (event.deltaY > 0) {
      arrowDown();
    }
  });
}

function arrowRight() {
  allContentList = allContentList.filter(
    (value, index, self) =>
      index === self.findIndex((t) => t.title === value.title)
  );

  if (isKeepWatchingContainerOpen) {
    addClass("restart-button", "selected-button");
    removeClass("continue-button", "selected-button");
  } else if (isExitAppContainerOpen) {
    addClass("no-button", "selected-button");
    removeClass("yes-button", "selected-button");
  } else {
    let verticalCursorPosition = verticalCursor;
    const limitValue = hasLiveChannels ? 6 : 5;
    navigationOnPageRL(headerCursor + 1 <= limitValue, 1, true);
    navigationOnCarousel(true);
    const itemList = allContentList[verticalCursorPosition - 2];
    if (verticalCursorPosition >= 2) {
      if (horizontalCursor + 1 >= itemList.length) return;
      if (
        horizontalCursor + 1 <
        allContentList[verticalCursorPosition - 2].result.list.length
      ) {
        swiper2[verticalCursorPosition - 2].slideNext(300);
        horizontalCursor += 1;

        if (
          horizontalCursor == itemList.length - 2 &&
          itemList.id != 21 &&
          itemList.id != 60 &&
          itemList.id != 76
        ) {
          buildNewItems(itemList, verticalCursorPosition);
        }
        setActiveItem();
      }

      if (verticalCursorPosition == 1 && swiper.realIndex == 4) {
        removeClass("carousel-button4", "button-selected");
        addClass("carousel-button0", "button-selected");
        swiper.slideTo(0, 600, false);
      }
    }
  }
}

function arrowLeft() {
  if (isKeepWatchingContainerOpen) {
    addClass("continue-button", "selected-button");
    removeClass("restart-button", "selected-button");
  } else if (isExitAppContainerOpen) {
    addClass("yes-button", "selected-button");
    removeClass("no-button", "selected-button");
  } else {
    navigationOnPageRL(headerCursor - 1 >= 0, -1, false);
    navigationOnCarousel(false);
    if (verticalCursor >= 2) {
      if (horizontalCursor - 1 >= 0) {
        horizontalCursor -= 1;
        swiper2[verticalCursor - 2].slidePrev(300);
      }
      setActiveItem();
    }
  }
}

function arrowUp() {
  if (!isKeepWatchingContainerOpen && !isExitAppContainerOpen) {
    horizontalCursor = 0;

    if (verticalCursor - 2 >= 0)
      swiper2[verticalCursor - 2]?.slideTo(0, 600, false);

    setVerticalCursor(verticalCursor - 1 >= 0, -1);
    navegationOutHeader(true);
    navigationOnPageUD();
    navigationOnCarousel(true);

    if (verticalCursor === 1) {
      goingTo("header-to-go");
    }
  }
}

async function arrowDown() {
  if (
    verticalCursor >= allContentList.length + 1 ||
    isKeepWatchingContainerOpen ||
    isExitAppContainerOpen
  )
    return;

  if (isBuilding) {
    await buildNextRemainingRow();
    isBuilding = true;
  }
  if (!isKeepWatchingContainerOpen && !isExitAppContainerOpen) {
    horizontalCursor = 0;
    let count = document.getElementsByClassName("container--section").length;
    if (verticalCursor <= count) {
      setVerticalCursor(verticalCursor <= lists.lists.length, 1);
      navegationOutHeader(false);
      navigationOnPageUD();
      navigationOnCarousel(false);

      let cursorToSlide = verticalCursor - 2;
      swiper2[cursorToSlide + 1]?.slideTo(0, 600, false);
      swiper2[cursorToSlide - 1]?.slideTo(0, 600, false);
      if (verticalCursor - 3 >= 0)
        swiper2[verticalCursor - 3]?.slideTo(0, 600, false);
    }
  }
}

function enterAction() {
  if (onModal || isKeepWatchingContainerOpen) {
    modalCompare();
  } else if (isExitAppContainerOpen) {
    let isModal = document.getElementsByClassName(" selected-button")[1].id;
    if (isModal == "yes-button") {
      exitApp();
    } else {
      closeExitAppModal();
    }
  } else {
    switch (verticalCursor) {
      case 0:
        enterOnHeader();
        break;
      case 1:
        settingIdsDetails(
          mainBanner[swiper.realIndex].id,
          mainBanner[swiper.realIndex].type
        );
        goTo("../details/index.html");
        break;
      default:
        let elementToShow =
          allContentList[verticalCursor - 2].result.list[horizontalCursor];
        if (isLive(elementToShow.type)) {
          settingIdsWatch(
            elementToShow.id,
            elementToShow.type,
            1,
            elementToShow.entry_id
          );
          setLocalStorage("titulo", elementToShow.title);
          if (elementToShow.id === 0) {
            goTo("../live/index.html");
          } else {
            goTo("../watch/index.html");
          }
        } else {
          if (
            allContentList[verticalCursor - 2].title != "Continuar assistindo"
          ) {
            settingIdsDetails(elementToShow.id, elementToShow.type);
            goTo("../details/index.html");
          } else {
            openKeepWatchingContainer();
          }
        }
        break;
    }
  }
}

function enterOnHeader() {
  if (hasLiveChannels) {
    switch (headerCursor) {
      case 0:
        break;
      case 1:
        goTo("../movie/index.html");
        break;
      case 2:
        goTo("../series/index.html");
        break;
      case 3:
        goTo("../live/index.html");
        break;
      case 4:
        goTo("../favorites/index.html");
        break;
      case 5:
        goTo("../filters/index.html");
        break;
      case 6:
        goTo("../profile-user/index.html");
        break;
      default:
        break;
    }
  } else {
    switch (headerCursor) {
      case 0:
        goTo("../home/index.html");
        break;
      case 1:
        goTo("../movie/index.html");
        break;
      case 2:
        goTo("../series/index.html");
        break;
      case 3:
        goTo("../favorites/index.html");

        break;
      case 4:
        goTo("../filters/index.html");
        break;
      case 5:
        goTo("../profile-user/index.html");
        break;
    }
  }
}

function navigationOnPageUD() {
  removeClass("carousel-button" + swiper.realIndex, "button-selected");
  if (verticalCursor >= 2) {
    goingTo(`container-section${verticalCursor - 2}`);
    setActiveItem();
  }
}

function setActiveItem() {
  let titleItem = document.getElementsByClassName("container--section-title")[
    verticalCursor - 2
  ].innerHTML;
  let element;
  activeItem.style.border = "";
  if (verticalCursor == 2 && hasLiveChannels) {
    let element = document.getElementById(`container-live${horizontalCursor}`);
    element.style.border = "8px solid #e86041";
    activeItem = element;
    return;
  }

  if (titleItem == "Continuar assistindo") {
    element = document.getElementById(
      `container--keep-watching${horizontalCursor}`
    );
  } else {
    element = document.getElementById(
      `container-films-series-section-track${verticalCursor - 2
      }-${horizontalCursor}`
    );
  }

  element.style.border = "8px solid #e86041";
  activeItem = element;
}

function settingSelected(valor, valorCursor, className) {
  if (verticalCursor === valor) {
    let containerLive = document.getElementsByClassName(className);
    containerLive[cursor]?.classList.add("section-selected");
    containerLive[cursor + valorCursor]?.classList.remove("section-selected");
  }
}

function settingIdsDetails(savedId, savedType) {
  setLocalStorage("id", savedId);
  setLocalStorage("tipo", savedType);
}

function settingIdsWatch(savedId, savedType, position, savedEntryId) {
  setLocalStorage("id", savedId);
  setLocalStorage("tipo", savedType);
  setLocalStorage("position", position);
  setLocalStorage("id_entry", savedEntryId);
}

function openKeepWatchingContainer() {
  isKeepWatchingContainerOpen = true;
  document.getElementsByClassName("keep-watching-container")[0].style.display =
    "flex";
}

function openExitAppContainer() {
  isExitAppContainerOpen = true;
  document.getElementsByClassName("exit-app-container")[0].style.display =
    "flex";
}

async function settingToWatch() {
  let idFilmSerie =
    dataList.keep_watching[swiper2[keepwatchingSwiperIndex].realIndex].id;
  let tipo =
    dataList.keep_watching[swiper2[keepwatchingSwiperIndex].realIndex].type;
  let position =
    dataList.keep_watching[swiper2[keepwatchingSwiperIndex].realIndex].position;
  let title;
  let episode;
  let entryId;
  let data = await axios(
    "https://watch.tv.br/v3/title?id_perfil=" +
    idPerfil +
    "&id=" +
    idFilmSerie +
    "&tipo=" +
    tipo +
    "&token=" +
    token,
    { method: "POST" }
  );

  data = data.data;

  if (tipo === "serie") {
    let found = data.seasons.find(
      (element) =>
        element.order == dataList.keep_watching[horizontalCursor].season_order
    );

    let found2 = found.videos.find(
      (element) =>
        element.order == dataList.keep_watching[horizontalCursor].episode_order
    );
    entryId = found2.id_kaltura;
    title = found.title;
    episode = found2.order;

    setLocalStorage("duration", found2.duration);
    settingTitleEp(title, episode);
  } else {
    setLocalStorage("duration", data.duration);
    entryId = data.id_kaltura;
  }

  let isModal = document.getElementsByClassName(" selected-button")[0].id;
  if (isModal == "continue-button")
    settingIdsWatch(idFilmSerie, tipo, position, entryId);
  else settingIdsWatch(idFilmSerie, tipo, 0, entryId);
  goTo("../watch/index.html");
}

function settingTitleEp(title, episode) {
  setLocalStorage("titulo", title);
  setLocalStorage("episodio", "Episódio " + episode);
}

function replaceImage(id, valor, imgReplace, imgReplaced) {
  let img = document.getElementById(id);

  if (hasLiveChannels) {
    headerCursor === valor ? (img.src = imgReplace) : (img.src = imgReplaced);
  } else {
    headerCursor + 1 === valor
      ? (img.src = imgReplace)
      : (img.src = imgReplaced);
  }
}

function buildSections(id, object) {
  let containerSectionTrack = document.getElementById(id);

  object.list.forEach(function (element, index) {
    let container = createTagDiv();
    container.classList.add("container--films-series");
    container.classList.add("swiper-slide");
    container.classList.add(id + "-content");
    container.id = "container-films-series-" + id + "-" + index;

    let containerSection = createTagDiv();
    containerSection.classList.add("container--films-series-content");

    let containerImg = createTagDiv();
    containerImg.classList.add("container--films-series-img");
    containerImg.onclick = () => {
      setLocalStorage("id", element.id);
      setLocalStorage("tipo", element.type);
      goTo("../details/index.html");
    };

    let img = createTagImg();
    img.classList.add("films-series-img");
    img.src = buildUrlImage(element.cdn, element.cover);

    containerImg.appendChild(img);
    containerSection.appendChild(containerImg);
    container.appendChild(containerSection);

    containerSectionTrack.appendChild(container);
  });
}

function buildUrlImage(cdnId, imageName) {
  let urlCdn = mainList.cdnlist.find((cdn) => cdn.id === cdnId);
  return urlCdn?.value
    .replace("{image}", imageName)
    .replace("{ks}", dataList.ks);
}

function buildCarousel() {
  let bannersTrack = document.getElementById("banners-track");

  mainBanner.forEach(function (element, index) {
    let carouselSlide = createTagDiv();
    carouselSlide.classList.add("carousel-slide");
    carouselSlide.classList.add("swiper-slide");
    carouselSlide.id = "carousel-slide" + index;

    let carouselDivImg = createTagDiv();
    carouselDivImg.classList.add("carousel--container-img");

    let carouselDiv = createTagDiv();
    carouselDiv.classList.add("container-img");

    let imgCarousel = createTagImg();

    imgCarousel.src = buildUrlImage(element.cdn, element.images.p1024x420);

    carouselDivImg.appendChild(carouselDiv);
    carouselDivImg.appendChild(imgCarousel);

    let titleContainer = createTagDiv();
    titleContainer.classList.add("carousel-tb");

    let title = createTagH1();
    title.classList.add("carousel-title");
    title.innerHTML = element.title;

    let button = createTagButton();
    button.classList.add("carousel-button");
    button.id = "carousel-button" + index;

    button.innerText = "Assistir";
    button.onclick = () => {
      settingIdsDetails(element.id, element.type);
      goTo("../details/index.html");
    };

    titleContainer.appendChild(title);
    titleContainer.appendChild(button);

    carouselSlide.appendChild(carouselDivImg);
    carouselSlide.appendChild(titleContainer);

    bannersTrack.appendChild(carouselSlide);
  });
}

function navigationOnCarousel(arrowRight) {
  if (verticalCursor === 1 || (!hasBanner() && verticalCursor === 0)) {
    activeItem.style.border = "";
    if (arrowRight) {
      removeClass(
        "carousel-button" + (swiper.realIndex - 1),
        "button-selected"
      );
      addClass("carousel-button" + swiper.realIndex, "button-selected");
    } else {
      removeClass(
        "carousel-button" + (swiper.realIndex + 1),
        "button-selected"
      );
      addClass("carousel-button" + swiper.realIndex, "button-selected");
    }
  }
}

function navigationOnSections(arrowRight, valor, id, id2, id3, classSelected) {
  if (verticalCursor === valor) {
    if (arrowRight) {
      removeClass(id2, classSelected);
      addClass(id, classSelected);
    } else {
      removeClass(id3, classSelected);
      addClass(id, classSelected);
    }
  }
}

function navegationOutHeader(arrowUp) {
  switch (headerCursor) {
    case 5:
      if (arrowUp)
        replaceImage("item-5-img", 5, imgSearchSelected, imgSearchUnselected);
      else
        replaceImage("item-5-img", 5, imgSearchUnselected, imgSearchSelected);
      break;
    case 6:
      if (arrowUp)
        replaceImage("item-6-img", 6, imgSettingSelected, imgSettingUnselected);
      else
        replaceImage("item-6-img", 6, imgSettingUnselected, imgSettingSelected);
      break;
    default:
      if (arrowUp && verticalCursor === 0)
        addClass("item-" + headerCursor, "selected-item");
      else removeClass("item-" + headerCursor, "selected-item");
      break;
  }
}

function navigationOnHeader() {
  if (!hasLiveChannels) {
    switch (headerCursor) {
      case 0:
        removeClass("item-1", "selected-item");
        addClass("item-0", "selected-item");
        break;
      case 1:
        removeClass("item-0", "selected-item");
        removeClass("item-2", "selected-item");
        addClass("item-1", "selected-item");
        break;
      case 2:
        removeClass("item-3", "selected-item");
        removeClass("item-4", "selected-item");
        removeClass("item-1", "selected-item");
        addClass("item-2", "selected-item");
        break;
      case 3:
        removeClass("item-3", "selected-item");
        removeClass("item-2", "selected-item");

        addClass("item-4", "selected-item");
        replaceImage("item-5-img", 5, imgSearchSelected, imgSearchUnselected);
        break;
      case 4:
        removeClass("item-4", "selected-item");
        replaceImage("item-5-img", 5, imgSearchSelected, imgSearchUnselected);
        replaceImage("item-6-img", 6, imgSettingSelected, imgSettingUnselected);
        break;
      case 5:
        replaceImage("item-5-img", 5, imgSearchSelected, imgSearchUnselected);
        replaceImage("item-6-img", 6, imgSettingSelected, imgSettingUnselected);

        break;
    }
  } else {
    switch (headerCursor) {
      case 0:
        removeClass("item-1", "selected-item");
        addClass("item-0", "selected-item");
        break;
      case 1:
        removeClass("item-0", "selected-item");
        removeClass("item-2", "selected-item");
        addClass("item-1", "selected-item");
        break;
      case 2:
        removeClass("item-3", "selected-item");
        removeClass("item-4", "selected-item");
        removeClass("item-1", "selected-item");
        addClass("item-2", "selected-item");
        break;
      case 3:
        removeClass("item-2", "selected-item");
        removeClass("item-4", "selected-item");
        addClass("item-3", "selected-item");
        break;
      case 4:
        removeClass("item-3", "selected-item");
        removeClass("item-2", "selected-item");

        addClass("item-4", "selected-item");
        replaceImage("item-5-img", 5, imgSearchSelected, imgSearchUnselected);
        break;
      case 5:
        removeClass("item-4", "selected-item");
        replaceImage("item-5-img", 5, imgSearchSelected, imgSearchUnselected);
        replaceImage("item-6-img", 6, imgSettingSelected, imgSettingUnselected);
        break;
      case 6:
        replaceImage("item-5-img", 5, imgSearchSelected, imgSearchUnselected);
        replaceImage("item-6-img", 6, imgSettingSelected, imgSettingUnselected);
        break;
      default:
        break;
    }
  }
}

function navigationOnPageRL(condition, valor, arrowRight) {
  switch (verticalCursor) {
    case 0:
      setHeaderCursor(condition, valor);
      navigationOnHeader();
      break;
    case 1:
      arrowRight ? swiper.slideNext(300) : swiper.slidePrev(300);
      break;

    default:
      break;
  }
}

