async function getList(id, page) {
  let body;

  if (page) {
    body = {
      id,
      id_perfil: idPerfil,
      page: page,
      size: 15,
    };
  } else {
    body = {
      id,
      id_perfil: idPerfil,
      page: 1,
      size: 15,
    };
  }

  let headers = {
    headers: {
      Authorization: "Bearer " + token,
      "Content-Type": "application/json",
    },
  };

  let list = await axios.post("https://watch.tv.br/v3/list", body, headers);

  list = list.data;
  if (id === 60 && list.list.length > 0) {
    list.list.push({
      id: 0,
      order: 5,
      title: "",
      type: "LIVE",
      highlight: "",
      cover: "",
      watermark: "",
      url: null,
      cdn: 5,
      entry_id: "",
      genre: "",
      iOSid: null,
      tagiOS: null,
      androidid: null,
      list: null,
      destiny: "",
      grid: "",
    });
    hasLiveChannels = true;
    window.localStorage.setItem("hasLiveChannels", true);
  } else if (id === 60 && list.list.length === 0) {
    window.localStorage.setItem("hasLiveChannels", false);
    hasLiveChannels = false;

    const liveOptionHeader = document.getElementById("item-3");
    liveOptionHeader.style.display = "none";
  }
  return list;
}
