async function getLists() {
  let toTheEnd = -1;
  let body = {
    id_perfil: idPerfil,
    id: 5,
  };

  let headers = {
    headers: {
      Authorization: "Bearer " + token,
      "Content-Type": "application/json",
    },
  };

  let lists = await axios.post("https://watch.tv.br/v3/lists", body, headers);

  lists = lists.data;
  lists.lists = lists.lists.filter(
    (element) => element.title != "Canais" && element.id != 76
  );
  remainsToBuildLists = { ...lists };
  lists.lists = lists.lists.slice(0, 5);
  remainsToBuildLists.lists = remainsToBuildLists.lists.slice(
    5,
    remainsToBuildLists.lists.length
  );

  for (let element of lists.lists) {
    let result;
    if (element.id == 21) {
      result = await getListKeep(element.id);
    } else {
      result = await getList(element.id);
      if (element.id == 60 && result?.list.length > 0) {
        setHasLiveChannels(true);
      }
    }

    if (result.list != 0) {
      allContentList.push({
        result,
        title: element.title,
        id: element.id,
        maxPage: result.pages,
        order: element.order,
        page: 1,
        length: result.list.length,
      });
      allContentList.sort(function (a, b) {
        return a.order - b.order;
      });
    }
  }
  if (!hasLiveChannels) {
    setHasLiveChannels(false);
  }
  return lists;
}
