function setCursor(condition, valor) {
  if (condition) cursor += valor;

  return cursor;
}

function setHeaderCursor(condition, valor) {
  if (condition) headerCursor += valor;

  return headerCursor;
}

function setVerticalCursor(condition, valor) {
  if (!hasBanner() && verticalCursor + valor == 1 && valor > 0)
    setVerticalCursor(true, ++valor);
  else if (!hasBanner() && verticalCursor + valor == 1 && valor < 0)
    setVerticalCursor(true, --valor);
  else if (condition) verticalCursor += valor;

  return verticalCursor;
}

