async function buildNextRemainingRow() {
  isBuilding = false;

  while (!(await getRowDataAndBuildNewRow()));
}

function buildNewRow() {
  actualBuildIndex++;

  let container = document.getElementById("container");
  let sectionTitle = document.getElementsByClassName(
    "container--section-title"
  );

  newContentList.forEach(function (element, index) {
    let verificationTitle = false;
    for (let index = 0; index < sectionTitle.length; index++) {
      if (sectionTitle[index].innerHTML == element.title) {
        actualBuildIndex--;
        verificationTitle = true;
      }
    }

    if (verificationTitle == false) {
      let containerSection = createTagDiv();
      containerSection.classList.add("container--section");
      containerSection.id = "container-section" + actualBuildIndex;

      let containerSectionTitle = createTagH1();
      containerSectionTitle.classList.add("container--section-title");
      containerSectionTitle.id = "section-title";
      containerSectionTitle.innerHTML = element.title;

      let containerSectionSlider = createTagDiv();
      containerSectionSlider.classList.add("container--section-slider");

      let containerSectionList = createTagDiv();
      containerSectionList.classList.add("container--section-list");
      containerSectionList.classList.add("swiper2");
      containerSectionList.id = "container--section-list" + actualBuildIndex;

      let containerSectionTrack = createTagDiv();
      containerSectionTrack.classList.add("container--section-track");
      containerSectionTrack.classList.add("swiper-wrapper");
      containerSectionTrack.id = "section-track" + actualBuildIndex;

      containerSectionSlider.appendChild(containerSectionList);
      containerSectionList.appendChild(containerSectionTrack);

      containerSection.appendChild(containerSectionTitle);
      containerSection.appendChild(containerSectionSlider);

      container.appendChild(containerSection);

      if (
        newContentList[index].title != "Canais de TV" &&
        newContentList[index].title != "Continuar assistindo"
      ) {
        buildSections(
          "section-track" + actualBuildIndex,
          newContentList[index].result
        );
      } else if (newContentList[index].title == "Continuar assistindo") {
        buildSectionsKeepWatching(
          newContentList[index].result,
          "section-track" + actualBuildIndex
        );
        keepwatchingSwiperIndex = actualBuildIndex;
      } else {
        buildSectionLive(newContentList[index].result);
      }

      let swiper = new Swiper("#container--section-list" + actualBuildIndex, {
        slidesPerView: "auto",
        slidesOffsetAfter: 2000,
        spaceBetween: 15,
        observer: true,
      });

      swiper2.push(swiper);
    }
  });

  newContentList = [];
}
main();

async function buildNewItems(contentList, verticalCursorPosition) {
  if (contentList.page + 1 > contentList.maxPage) {
    contentList.page = 1;
  } else {
    contentList.page++;
  }

  let result = await getList(contentList.id, contentList.page);

  let id = `section-track${verticalCursorPosition - 2}`;

  let containerSectionTrack = document.getElementById(id);

  result.list.forEach(function (element, index) {
    index = index + contentList.length;
    let container = createTagDiv();
    container.classList.add("container--films-series");
    container.classList.add("swiper-slide");
    container.classList.add(id + "-content");
    container.id = "container-films-series-" + id + "-" + index;

    let containerSection = createTagDiv();
    containerSection.classList.add("container--films-series-content");

    let containerImg = createTagDiv();
    containerImg.classList.add("container--films-series-img");
    containerImg.onclick = () => {
      setLocalStorage("id", element.id);
      setLocalStorage("tipo", element.type);
      goTo("../details/index.html");
    };

    let img = createTagImg();
    img.classList.add("films-series-img");
    img.src = buildUrlImage(element.cdn, element.cover);

    containerImg.appendChild(img);
    containerSection.appendChild(containerImg);
    container.appendChild(containerSection);

    containerSectionTrack.appendChild(container);
  });
  contentList.length += result.list.length;

  const newList = contentList.result.list.concat(result.list);
  contentList.result.list = newList;
}

function enterChannelNumber(digitedNumber) {
  clearInterval(digitNumberTimeOut);

  digitNumber = !digitNumber ? digitedNumber : digitNumber + digitedNumber;

  const displayNumber = document.getElementById("digited-channel");
  const displayNumberContainer = document.getElementById(
    "digited-channel-container"
  );
  displayNumber.innerText = digitNumber;
  displayNumberContainer.style.display = "block";
  digitNumberTimeOut = setTimeout(() => {
    goToDigitChannel();
  }, 3000);
}

async function goToDigitChannel() {
  let listChannels = await getList(60, 100);
  const displayNumber = document.getElementById("digited-channel");
  const displayNumberContainer = document.getElementById(
    "digited-channel-container"
  );

  const digtedChannel = listChannels.list.find(
    (item) => item.channel_number == parseInt(digitNumber)
  );

  if (!digtedChannel) {
    digitNumber = null;
    displayNumber.innerText = "";
    displayNumberContainer.style.display = "none";
    return;
  }

  localStorage.setItem("titulo", digtedChannel.title);
  localStorage.setItem("id", digtedChannel.id);
  localStorage.setItem("id_entry", digtedChannel.entry_id);
  localStorage.setItem("tipo", digtedChannel.type);

  goTo("../watch/index.html");

  digitNumber = null;
  displayNumber.innerText = "";
  displayNumberContainer.style.display = "none";
}

